#user-popup {
  text-align: center;
}

#user-popup input {
  width: 90%;
}

#user-popup input[type="checkbox"] {
  width: 25px;
}

#user-popup .uk-button {
  border-radius: 3px;
}

#user-popup .uk-flex-item-1 a {
  outline: none;
}

#user-popup .uk-flex-item-1 a span {
  color: #3498db;
}

#user-popup .uk-flex-item-1 a div {
  color: dimgrey;
}

body {
  margin: 0;
  padding: 0;
}

@media (max-width: 1480px) {
  #slideMenu {
    text-align: center;
  }
  #slideMenu .parent {
    margin-bottom: 10px;
  }
  #slideMenu .parent .text-menu {
    display: block;
  }
}

@media (max-width: 730px) {
  #slideMenu {
    text-align: center;
    padding-left: 18px;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  #slideMenu .parent {
    margin-bottom: 10px;
  }
  #slideMenu .parent .text-menu {
    display: none;
  }
  #slideMenu .parent .icon-menu {
    display: inline;
    font-size: 200%;
  }
  #slideMenu .parent a {
    text-align: center;
  }
  #content .landing_form {
    width: 90% !important;
    margin-left: 5% !important;
  }
  #content .landing_form legend {
    font-size: 20px !important;
    line-height: 40px !important;
  }
  #content .landing_form textarea {
    width: 90% !important;
  }
  #content .landing_form input {
    width: 90% !important;
  }
  #content .landing_form .gender {
    width: 20px !important;
  }
}

#page {
  height: 100vh;
}

#slideMenu {
  height: 100vh;
  background-color: #252526;
  color: white;
  padding-top: 10vh;
}

#slideMenu .parent {
  margin-bottom: 15px;
}

#slideMenu .parent a {
  color: white;
  margin: 0px;
  font-size: 16px;
}

#slideMenu .parent a:hover {
  background-color: #202021;
  border-radius: 5px;
}

#slideMenu .icon-menu {
  font-size: 26px;
  width: 50px;
  padding: 2px;
  display: inline-block;
}

#content {
  padding-left: 0px;
  height: 100vh;
}

#content #header {
  height: calc(10vh - 1px);
  text-align: center;
  border-bottom: 1px solid #AAAAAA;
}

#content #header .logo {
  height: 90%;
}

#content #pages {
  overflow: hidden;
  height: 90vh;
}

#content #pages .landing_pages_item {
  height: 90vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

#content #pages .landing_pages_item .landing_form {
  width: 60%;
  margin-left: 25%;
  text-align: center;
  padding: 5px;
}

#content #pages .landing_pages_item .landing_form legend {
  font-size: 40px;
  line-height: 60px;
}

#content #pages .landing_pages_item .landing_form input {
  width: 40%;
  border-radius: 3px;
  border: 1px solid #CECECE;
}

#content #pages .landing_pages_item .landing_form textarea {
  width: 40%;
  height: 100px;
  border-radius: 3px;
  border: 1px solid #CECECE;
}

#content #pages .landing_pages_item .landing_form button {
  border-radius: 3px;
  color: white;
}

#content #pages .landing_pages_item .landing_form select {
  border-radius: 3px;
  border: 1px solid #CECECE;
}

#accueil #presentation {
  background-color: rgba(255, 255, 255, 0.6);
  height: 90vh;
}

#expedition {
  background-image: url("../../images/motion-blur-wallpaper-2.jpg");
  background-position: center center;
  background-size: cover;
}

#expedition .landing_form {
